import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'

export class Departments extends Component {
    constructor(props) {
        super(props)

        this.state = {
            token: localStorage.getItem('myToken') || '',
            departmentData: [],
            keys: []
        }
    }

    componentDidMount = () => {
        fetch('http://employee.service.com/api/Departments', {
            method: "GET",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*",
                "Authorization": 'Bearer ' + this.state.token
            },

        }).then(res => {
            if (res.ok) {
                console.log("res:" + res)
                return res.json();
            } else {
                throw Error(res.statusText);
            }
        }).then(json => {
            this.setState({ departmentData: json, keys: Object.keys(json[0]) }, () => {
                console.log("departmentData:" + this.state.departmentData)
            })

        }).catch(error => console.error(error));
    }

    renderTableData() {
        return this.state.departmentData.map((department, index) => {
            const { ID, Name, Location } = department //destructuring
            return (
                <tr key={index}>
                    <td>{ID}</td>
                    <td>{Name}</td>
                    <td>{Location}</td>
                </tr>
            )
        })
    }

    renderTableHeader() {
        let header = this.state.keys
        console.log("header:" + header)
        return header.map((key, index) => {
            return <th key={index}>{key.toUpperCase()}</th>
        })
    }

    handleAddDepartment =() =>{
        this.props.history.push('/AddDepartment')
    }

    render() {
        const isLoggedIn = localStorage.getItem("myToken");
        if (!isLoggedIn)
            return <Redirect to='/' />

        return (<div className="table-responsive">
            <table className="col-12 h5 mb-0 pb-0">
                <tbody className="mb-0 pb-0">
                    <tr className="mb-0 pb-0">
                        <td>Departments Detail</td>
                        <td className="float-right mb-0 pb-0"><h6 className="mb-0 pb-0"><input onClick={this.handleAddDepartment} className="mb-0 pb-0 align-middle btn btn-success" style={{padding:"0 10px 10px 10px"}} type="button" value="Add Department"></input></h6></td>
                    </tr>
                </tbody>
            </table>
            <table className="table table-bordered table-striped table-hover table-condensed">
                <thead>
                    <tr className="bg-dark text-white">
                        {this.renderTableHeader()}
                    </tr>
                </thead>
                <tbody>
                    {this.renderTableData()}
                </tbody>
            </table>
        </div>)

    }
}

export default Departments



