import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'

export class Employees extends Component {
    constructor(props) {
        super(props)

        this.state = {
            token: localStorage.getItem('myToken') || '',
            employeeData: [],
            keys: []
        }
    }

    componentDidMount = () => {
        fetch('http://employee.service.com/api/Employees', {
            method: "GET",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*",
                "Authorization": 'Bearer ' + this.state.token
            },

        }).then(res => {
            if (res.ok) {
                console.log("res:" + res)
                return res.json();
            } else {
                throw Error(res.statusText);
            }
        }).then(json => {
            this.setState({ employeeData: json, keys: Object.keys(json[0]) }, () => {
                console.log("employeeData:" + this.state.employeeData)
            })

        }).catch(error => console.error(error));
    }
    renderTableData =() => {
        return this.state.employeeData.map((employee, index) => {
            const { ID, FirstName, LastName, Gender, Salary, Department, Location, JobTitle } = employee //destructuring
            return (
                <tr key={index}>
                    <td>{ID}</td>
                    <td>{FirstName+" "+ LastName}</td>
                    <td>{Gender}</td>
                    <td>{Salary}</td>
                    <td>{Department}</td>
                    <td>{Location}</td>
                    <td>{JobTitle}</td>
                </tr>
            )
        })
    }

    renderTableHeader= () => {
        let header = this.state.keys
        console.log("header:" + header)
        return header.map((key, index) => {
            if (key.toUpperCase() !== "FIRSTNAME" && key.toUpperCase() !== "LASTNAME") {
                return <th key={index}>{key.toUpperCase()}</th>
            }
            else if (key.toUpperCase() === "FIRSTNAME")
                return <th key={index}>NAME</th>
        })
    }

    handleAddEmployee = () => {
        this.props.history.push('/AddEmployee')
    }

    render() {
        const isLoggedIn = localStorage.getItem("myToken");
        if (!isLoggedIn)
            return <Redirect to='/' />

        return (<div className="table-responsive">
            <table className="col-12 h5 mb-0 pb-0">
                <tbody className="mb-0 pb-0">
                    <tr className="mb-0 pb-0">
                        <td>Employees Detail</td>
                        <td className="float-right mb-0 pb-0"><h6 className="mb-0 pb-0"><input onClick={this.handleAddEmployee} className="mb-0 pb-0 align-middle btn btn-success" style={{ padding: "0 10px 10px 10px" }} type="button" value="Add Employee"></input></h6></td>
                    </tr>
                </tbody>
            </table>
            <table className="table table-bordered table-striped table-hover table-condensed">
                <thead><tr className="bg-dark text-white">
                    {this.renderTableHeader()}
                </tr>
                </thead>

                <tbody>
                    {this.renderTableData()}
                </tbody>
            </table>
        </div>)
    }
}

export default Employees



