import React, { Component } from 'react';
import { Collapse, Container, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink } from 'reactstrap';
import { Link } from 'react-router-dom';
import './NavMenu.css';
import { Redirect } from 'react-router-dom'

export class NavMenu extends Component {
    static displayName = NavMenu.name;

    constructor(props) {
        super(props);

        this.toggleNavbar = this.toggleNavbar.bind(this);
        this.state = {
            collapsed: true
        };
    }

    toggleNavbar() {
        this.setState({
            collapsed: !this.state.collapsed
        }); 
    }

    render() {
        let LogOff, User;
        const isLoggedIn = localStorage.getItem("myToken");
        if (isLoggedIn) {
            LogOff = <NavLink tag={Link} className="text-dark" onClick={() => { localStorage.removeItem('myToken'); < Redirect to='/' /> }} to="/">Sign out</NavLink>
            User = <Link className="text-white navbar-nav" to='/Profile'>{localStorage.getItem("myUserName")}</Link>
        }
        return (
            <header>
                <Navbar className="bg-secondary navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3" light>
                    <Container>
                        <NavbarBrand className="text-white" tag={Link} to="/"><h2>Employee Portal</h2></NavbarBrand>
                        <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
                        <Collapse className="d-sm-inline-flex flex-sm-row-reverse" isOpen={!this.state.collapsed} navbar>
                            <ul className="navbar-nav flex-grow">
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" to="/">Home</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" to="/Employees">Employees</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" to="/Departments">Departments</NavLink>                                    
                                </NavItem>
                                <NavItem>{LogOff}</NavItem>
                            </ul>
                        </Collapse>
                        
                    </Container>  
                    {User}
                </Navbar>
                
            </header>
        );
    }
}



